/*===============================================
Main navigation - Co-op Front-end Toolkit
===============================================*/

.nav-primary {
  position: absolute;
  z-index: 100;
  width: 100%;
  height: 100%;
  height: 100vh;
  margin-left: 100%;
  background: $white;
  transition: all, 0.25s, ease-in-out;

  &.open {
    margin-left: 0;
  }

  @include media(medium) {
    position: static;
    margin-left: 0;
    height: auto;
    border: 1px solid $coop-border-grey;
    border-width: 1px 0;
    text-align: left;
  }

  ul {
    @include list-bare;
    margin: 0;

    @include media(medium) {
      li {
        position: relative;
        display: inline-block;

        &.active a {
          background: $offwhite;

          &:before {
            border-color: transparent;
          }
        }

        &.active + li,
        &:hover + li,
        &:first-child {
          a:before {
            border-color: transparent;
          }
        }
      }
    }

    a {
      display: block;
      padding: $quarter-spacing-unit;
      border-bottom: 0;
      color: $coop-blue;

      &:before {
        content: " ";
        position: absolute;
        left: 0;
        height: 24px;
        border-left: 1px solid $coop-border-grey;
        transition: all, .25s, ease-in-out;
      }

      &:hover,
      &:focus {
        background: $offwhite;

        &:before {
          border-color: transparent;
        }
      }

      &:focus {
        outline: 1px dotted $white;
      }

      @include media(medium) {
        color: $coop-link-blue;
      }
    }
  }
}

.nav-standout {
  ul {
    a {
      font-size: em-calc(38);

      @include media(medium) {
        font-size: em-calc(16);
      }
    }
  }
}

.nav-secondary {
  margin: 2em 0;
  margin-left: 0;
  text-align: center;

  ul {
    @include list-inline;
    border: 1px dashed $coop-grey;
    border-width: 1px 0;

    a {
      display: block;
      padding: $quarter-spacing-unit;
      border-bottom: 0;
      color: $coop-grey;
      font-weight: bold;

      &:hover,
      &:focus,
      &.active {
        background: $offwhite;
      }

      &:focus {
        outline: 1px dotted $white;
      }
    }
  }
}
